// pages/jobs.js
import { useEffect, useState } from "react";
import { motion } from "framer-motion";

export default function JobsPage() {
  const [jobs, setJobs] = useState([]);
  const [loading, setLoading] = useState(true);

  // ‚úÖ Fetch jobs from API
  useEffect(() => {
    async function fetchJobs() {
      try {
        const res = await fetch("/api/jobs");
        const data = await res.json();
        setJobs(data);
      } catch (err) {
        console.error("Error fetching jobs:", err);
      } finally {
        setLoading(false);
      }
    }
    fetchJobs();
  }, []);

  return (
    <div className="min-h-screen bg-gradient-to-b from-orange-50 to-white px-4 py-10">
      <div className="max-w-5xl mx-auto">
        <h1 className="text-3xl font-bold text-orange-600 text-center mb-10">
          üß∞ Available Jobs
        </h1>

        {loading ? (
          <p className="text-center text-gray-500">Loading jobs...</p>
        ) : jobs.length === 0 ? (
          <p className="text-center text-gray-500">
            No jobs have been posted yet.
          </p>
        ) : (
          <div className="grid md:grid-cols-2 gap-6">
            {jobs.map((job) => (
              <motion.div
                key={job.id}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.4 }}
                className="bg-white shadow-lg rounded-2xl p-6 border border-orange-100 hover:shadow-xl transition"
              >
                <h2 className="text-xl font-semibold text-gray-800">
                  {job.service}
                </h2>
                <p className="text-gray-600 mt-1">{job.description}</p>

                <div className="flex justify-between items-center mt-4">
                  <span className="text-orange-600 font-medium">
                    üí∞ ${job.budget}
                  </span>
                  <span className="text-sm text-gray-500">
                    üìç {job.district}
                  </span>
                </div>

                <div className="text-xs text-gray-400 mt-2">
                  {new Date(job.created_at).toLocaleString()}
                </div>
              </motion.div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}
